import { useState } from "react";
import "./App.css";
import Side from "./components/Side";
import Buttons from "./components/Buttons";
import DiceImage from "./components/DiceImage";

const App = () => {
  const [activePlayer, setActivePlayer] = useState("player-0");
  const [scorePlayer0, setScorePlayer0] = useState(0);
  const [scorePlayer1, setScorePlayer1] = useState(0);
  const [currentScore, setCurrentScore] = useState(0);
  const [diceImage, setDiceImage] = useState(null);
  const [isWinner, setIsWinner] = useState(null);

  const changePlayer = () => {
    setCurrentScore(0);
    activePlayer === "player-0"
      ? setActivePlayer("player-1")
      : setActivePlayer("player-0");
    setDiceImage(null);
  };
  const rollDice = () => {
    const rnd = Math.ceil(Math.random() * 6);
    setDiceImage(rnd);
    if (rnd !== 1) {
      setCurrentScore(currentScore + rnd);
    } else {
      changePlayer();
    }
  };
  const hold = () => {
    if (activePlayer === "player-0") {
      setScorePlayer0(scorePlayer0 + currentScore);
      if (scorePlayer0 + currentScore >= 20) {
        setIsWinner("player-0");
      }
    } else {
      setScorePlayer1(scorePlayer1 + currentScore);
      if (scorePlayer1 + currentScore >= 20) {
        setIsWinner("player-1");
      }
    }
    changePlayer();
  };
  const newGame = () => {
    setActivePlayer("player-0");
    setScorePlayer0(0);
    setScorePlayer1(0);
    setCurrentScore(0);
    setDiceImage(null);
    setIsWinner(null);
  };
  return (
    <div className="parent">
      <Side
        playerName="player 0"
        isActive={activePlayer === "player-0"}
        score={scorePlayer0}
        currentScore={activePlayer === "player-0" ? currentScore : 0}
        isWinner={isWinner === "player-0"}
      />
      <Side
        playerName="player 1"
        isActive={activePlayer === "player-1"}
        score={scorePlayer1}
        currentScore={activePlayer === "player-1" ? currentScore : 0}
        isWinner={isWinner === "player-1"}
      />
      <Buttons
        rollDice={rollDice}
        hold={hold}
        diceImage={diceImage}
        isWinner={isWinner}
        newGame={newGame}
      />
      <DiceImage diceImage={diceImage} />
    </div>
  );
};

export default App;
