import React from "react";

const Buttons = ({ rollDice, hold, diceImage, isWinner, newGame }) => {
  return (
    <>
      <button className="new" onClick={newGame}>
        new game
      </button>
      {!isWinner && (
        <button className="role" onClick={rollDice}>
          roll dice
        </button>
      )}

      {diceImage && (
        <button className="hold" onClick={hold}>
          hold
        </button>
      )}
    </>
  );
};

export default Buttons;
