import React from "react";

const Side = ({ isActive, score, currentScore, playerName, isWinner }) => {
  return (
    <div
      id={`${isWinner && "winner"}`}
      className={`player-0 side ${isActive ? "active" : null}`}
    >
      <div className="player-0-name">
        <span>{isWinner ? `${playerName + " winner"}` : `${playerName}`}</span>
      </div>
      <div className="player-0-score">{score}</div>
      <div className="player-0-sum">{currentScore}</div>
    </div>
  );
};

export default Side;
